// Application
#app {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto;
    grid-auto-flow: row;
    grid-auto-rows: auto;
    grid-gap: 0;
    min-height: 100vh;
    z-index: $zindex_app;

    @extend .sticker; // TODO: debug
}

.app-container {
    @extend .layout;
    @extend .grid-12;
}
